body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background-color: #e0e2e0; */
  /* font-family: "Yantramanav", sans-serif; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
/********** App styles */
.body-wrapper {
  /* display: flex;
  flex-direction: column;
  min-height: 100vh; */
}
.sticky-footer {
  /* flex-grow: 1; */
}

/********** Nav styles */
.main-header-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  /* max-width: 700px; */
}
.main-nav {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
.nav-padding {
  padding: 15px 20px;
}
/* unvisited link */
a {
  /* color: #ff0000; */
  color: #141313;
}
/* visited link */
a:visited {
  /* color: #00ff00; */
  color: #141313;
}
/* mouse over link */
a:hover {
  color: #a7a8a8;
  /* color: blue; */
  /* border-bottom-color: #5b7484; */
}
a:hover[href="https"] {
  background-color: #be561f;
}
/* selected link */
a:active {
  /* color: #0000ff; */
  /* color: #5b7484; */
  /* color: #636262; */
  /* border-bottom-color: #5b7484; */
}
a.underline {
  border-bottom: solid #a7a8a8 1px;
}
/********** Cool link  */
.cool-link {
  /* display: inline-block; */
  color: #a7a8a8;
  /* text-decoration: none; */
}

.cool-link::after {
  content: "";
  display: block;
  width: 0;
  height: 1px;
  background: #a7a8a8;
  /* background: #5b7484; */
  transition: width 0.3s;
}

.cool-link:hover::after {
  width: 100%;
  /* transition: width .3s; */
}
/********** Home styles */
.home-content-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  max-width: 90%;
  -webkit-align-items: center;
          align-items: center;
  margin: auto;
  /* background-color: #ded0b7; */
}
.home-title {
  margin: 150px 0;
  /* TODO:  =>  should centre with auto??? not with 150px */
  text-align: center;
}
.wave {
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
  -webkit-text-decoration-style: wavy;
          text-decoration-style: wavy;
}
.light-font {
  font-weight: lighter;
}
/********** Contact styles */
.contact-nav {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
}
.contact-title {
  margin-top: 150px;
}
/********** Projects styles */
.project-content {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.project-list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  max-width: 689px;
}
.project-item {
  width: 225px;
  height: 175px;
  background-color: #e0e2e0;
  margin: 15px;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
/********** Footer styles */
footer {
  padding: 50px;
}
/********** Media Queries */
/* // Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) {
  .main-header-container {
    max-width: 90%;
    margin: auto;
  }
  .lg-item {
    -webkit-flex-grow: 10;
            flex-grow: 10;
  }
  .home-title {
    text-align: left;
  }
}

/* // Medium devices (tablets, 768px and up) */
@media (min-width: 768px) {
  .main-header-container {
    max-width: 80%;
    margin: auto;
  }
  .home-content-wrapper {
    max-width: 70%;
  }
}

/* // Large devices (desktops, 992px and up) */
@media (min-width: 950px) {
  .main-header-container {
    max-width: 950px;
    margin: auto;
  }
}

